import Vue from 'vue'
import VueRouter from 'vue-router'

import login from './components/login'
import articleHome from './components/article-home'
import pageNotFound from './components/not-found'
import articleList from './components/article-list'
import dashboard from './components/dashboard'
import articleIncrease from './components/article-increase'

Vue.use(VueRouter)

export default new VueRouter({
    // mode: "history",
    routes: [
        { path: '/login', component: login },
        { path: '/articleHome',
            component: articleHome, 
            children: [
                { path: '', component: dashboard, meta:{ requireAuth: true },},
                { path: '/article-list', component: articleList, meta:{ requireAuth: true }, },
                { path: '/article-increase', component: articleIncrease, meta:{ requireAuth: true }, },
            ]       
        },
        { path : '', component: login },
        { path: '*', component: pageNotFound }
    ]
})
