<template>
  <div class="article-increase">
    <h6>{{ id == ''? '新增' : '编辑'}}Article</h6>
    <el-form :model="ruleForm" 
             :rules="rules" ref="ruleForm" 
             label-width="100px"
             class="demo-ruleForm pl85 mt36 mb20">
        <el-form-item label="标题名称" prop="title" class="w60">
            <el-input v-model="ruleForm.title"></el-input>
        </el-form-item>
        <el-form-item label="类型" prop="type">
            <!-- <el-select v-model="ruleForm.type">
                <el-option v-for="(type, index) in typeOption"
                           :label="type.label"
                           :value="type.value"
                           :key="index"></el-option> -->
                <!-- <el-option label="全部" value=""></el-option>
                <el-option label="首页Banner" value="0"></el-option>
                <el-option label="找职位Banner" value="1"></el-option>
                <el-option label="找精英Banner" value="2"></el-option>
                <el-option label="行业大图" value="3"></el-option> -->
            <!-- </el-select> -->
            <select v-model="ruleForm.type" class="select-type">
                <option v-for="(type, index) in typeOption"
                        :value="type.value"
                        :key="index">{{type.label}}</option>
            </select>
        </el-form-item>
        <el-form-item label="" prop="industry">
            <!-- <el-select v-model="ruleForm.industry">
                <el-option label="请选择" value=""></el-option>
                <el-option label="移动互联网" value="0"></el-option>
                <el-option label="电子商务" value="1"></el-option>
                <el-option label="企业服务" value="2"></el-option>
                <el-option label="O2O" value="3"></el-option>
                <el-option label="教育" value="4"></el-option>
                <el-option label="金融" value="5"></el-option>
                <el-option label="游戏" value="6"></el-option>
            </el-select> -->
            <select class="select-industry" v-model="ruleForm.industry">
                <option value="">请选择</option>
                <option value="0">移动互联网</option>
                <option value="1">电子商务</option>
                <option value="2">企业服务</option>
                <option value="3">O2O</option>
                <option value="4">教育</option>
                <option value="5">金融</option>
                <option value="6">游戏</option>
            </select>
        </el-form-item>
        <el-form-item label="说明" class="w60">
            <el-input v-model="ruleForm.content"></el-input>
        </el-form-item>
        <el-form-item label="跳转链接" prop="url" class="w60">
            <el-input v-model="ruleForm.url"></el-input>
        </el-form-item>
        <div class="increase-choose ml25">
            <div class="mb10 choose-option">
                <span><span class="cd9 ml25"></span>配图</span>
                <input type="file" value="选择文件" 
                       accept=".png, .jpeg, .jpg" 
                       class="br5 option ml25" id="chooseFile"
                       ref="chooseFile"
                       @change="handleFiles">
                <label for="chooseFile" class="br5 option ml25">选择文件</label>
            </div>
            <div class="mb10 ml75 option-preview">
                <span class="ml25">配图预览</span>
                <img class="preview-img" :src="img">
            </div>
            <div class="mb10 ml75 option-upload">
                <div class="upload-img">
                    <span>图片名</span>
                    <span>文件大小</span>
                    <span>进度</span>
                    <span>操作</span>
                    <span>操作</span>
                </div>
                <div class="upload-show">
                    <div class="wb">{{imgName}}</div>
                    <div>{{imgSize}}</div>
                    <div class="progress-box">
                        <div class="upload-progress">
                            <div class="progress-show" :style="{ width: upProgress }"></div>
                        </div>
                    </div>
                    <div>
                        <li class="el-icon-circle-check" v-show="hasUpload"></li>
                        <!-- <img src="../../assets/success.png"> -->
                        <!-- <img src="../../assets/fail.png"> -->
                    </div>
                    <div>
                        <input type="button"
                               value="上传"
                               class="upload-down"
                               :disabled="canUpload"
                               @click="upLoadImg"
                               >
                        <input type="button" value="删除"
                               class="upload-remove"
                               @click="cancelLoad"
                               >
                    </div>
                </div>
            </div>
            <div class="ml75 option-add">
                <input type="button" value="立即上线" class="br5"
                       @click="submitOnline">
                <input type="button" value="存为草稿" class="br5"
                       @click="submitDrafts">
                <input type="button" value="取消"
                       class="br5 add-cancel"
                       @click="cancelEdit">
            </div>
        </div>
    </el-form>
  </div>
</template>

<script>

export default {
    data: function() {
        return {
            file: {},
            hasUpload: false,
            canUpload: true,
            imgName: '',
            imgSize: '',
            upProgress: '',
            id: '',
            isType: false,
            imgSrc: 'undefined',
            createAt: '',
            img: '1',
            ruleForm: {
                title: '',
                type: '',
                industry: '',
                content: '',
                url: '',
            },
            rules: {
                title: [
                    { required: true, message: '请输入标题名称', trigger: 'blur' },
                    { min: 2, max: 8, message: '长度在 2 到 8 个字符', trigger: 'blur' }
                ],
                type: [
                    { required: true, message: '请选择类型', trigger: 'change' }
                ],
                industry: [
                    { required: false, message: '类型为行业大图时，该项必填', trigger: 'change' }
                ],
                url: [
                    { required: true, message: '请输入跳转链接', trigger: 'blur' }
                ]
            },
            typeOption: [
                { label: "全部", value: "" },
                { label: "首页Banner", value: "0" },
                { label: "找职位Banner", value: "1" },
                { label: "找精英Banner", value: "2" },
                { label: "行业大图", value: "3" }
            ]
        }
    },
    watch: {
        'ruleForm.type': function() {
            if (this.ruleForm.type == 3) {
                // this.isType = true
                this.rules.industry[0].required = true
            }else {
                this.rules.industry[0].required = false
            }
        }     
    },
    created: function() {
        let id = this.$route.query.id
        if (id) {
            this.id = id
            this.$axios.get(`api/a/article/${id}`)
            .then(res => {
                if (res.data.code ==0) {
                    let returnData = res.data.data.article
                    this.ruleForm.title = returnData.title
                    this.ruleForm.type = returnData.type
                    this.img = returnData.img
                    this.ruleForm.content = returnData.content
                    this.ruleForm.url = returnData.url
                }
            })
            .catch(error=>{
              alert(error);
            })
        }
    },
    computed: {
    },

    methods: {
      submitForm() {
        //   console.log(this.$refs.ruleForm);
        //   console.log(this.$refs[formName]);
        this.$refs.ruleForm.validate((valid) => {
          if (valid) {
              if (this.id) {
                  this.ruleForm.img = this.img
                  this.ruleForm.createAt = Date.now()
                //   console.log(this.ruleForm);
                //   console.log(this.$qs.stringify(this.ruleForm));
                  this.$axios.put(`api/a/u/article/${this.id}`, this.$qs.stringify(this.ruleForm))
                  .then(res => {
                      if (res.data.code == 0) {
                          alert("操作成功！")
                          this.$router.push('/article-list')
                      }else {
                          alert("操作失败！，错误码" + res.data.code)
                      }
                  })
                  .catch(error=>{
                       alert(error);
                  })
              }else {
                //   console.log(this.ruleForm);
                //   console.log(this.$qs.stringify(this.ruleForm));
                  this.ruleForm.img = this.imgSrc
                  this.$axios.post(`api/a/u/article`, this.$qs.stringify(this.ruleForm))
                  .then(res => {
                      if (res.data.code == 0) {
                          alert("操作成功！")
                          this.$router.push('/article-list')
                      }else {
                          alert("操作失败！，错误码" + res.data.code)
                      }
                  })
                  .catch(error=>{
                      alert(error);
                  })
              }
            // alert('编辑成功!');
          } else {
            alert('提交失败！有必填项未填写');
            return false;
          }
        });
      },
      //立即上线
      submitOnline() {
          this.ruleForm.status = 2;
          this.submitForm()
      },
      //存为草稿
      submitDrafts() {
          this.ruleForm.status = 1;
          this.submitForm()
      },
      //取消操作
      cancelEdit() {
          alert("确定取消操作吗？")
          this.$router.push('/article-list')
      },
      //转化图片文件大小
      returnFileSize(number) {
            if(number < 1024) {
            return number + 'bytes';
            } else if(number > 1024 && number < 1048576) {
            return (number/1024).toFixed(1) + 'KB';
            } else if(number > 1048576) {
            return (number/1048576).toFixed(1) + 'MB';
            }
      },

      cancelLoad() {
        alert("请选择新的图片上传");
        this.img = '';
        this.imgSrc = '';
        this.imgName = '';
        this.imgSize = '';
        this.upProgress = 0
      },
      //选择需要上传的文件
      handleFiles() {
            var that = this
            this.file = this.$refs.chooseFile.files[0];
            this.imgName = this.file.name;
            this.canUpload = false;
            let reader = new FileReader();
            this.imgSize = this.returnFileSize(this.file.size);
            if (this.file) {
              reader.readAsDataURL(this.file);
              reader.onload = function() {
                // console.log(e);
                // console.log(this.result);
                that.img = this.result
              }
            
        }
      },
      
      upLoadImg() {
          var form = new FormData()
          form.append('file', this.file)
          let config = {
            onUploadProgress: progressEvent => {
            var complete = ((progressEvent.loaded / progressEvent.total) * 100).toFixed(1) + '%'
            this.upProgress = complete
            }
          }
          this.canUpload = true
          this.$axios.post('api/a/u/img/task', form, config)
          .then(res => {
              if (res.data.code == 0) {
                //   console.log(res);
                  this.imgSrc = res.data.data.url
                  this.hasUpload = true
              }else {
                  alert("上传失败！")
              }
          })
          .catch(error=>{
              alert(error);
          })
      },
}
    
            
        
}
</script>

<style scoped>
.pl85 {
    padding-left: 85px;
}
.mt36 {
    margin-top: 36px;
}
/* .db {
    display: inline-block;
} */
.mb20 {
    margin-bottom: 20px;
}
.mb10 {
    margin-bottom: 10px;
}
.w60 {
    width: 60%;
    /* display: inline-block; */
}
.cd9 {
    color: #d9534f;
}
.ti2 {
    display: inline-block;
    text-indent: 2em;
}
.ml25 {
    margin-left: 25px;
}
.ml75 {
    margin-left: 75px;
}
.br5 {
    padding: 5px;
    border: 1px solid #ddd;
    border-radius: 5px;
}
.wb {
    word-break: break-all;
}
.select-type,
.select-industry {
    padding: 10px;
    width: 220px;
    height: 40px;
    border: 1px solid #ddd;
    border-radius: 5px;
}
.article-increase {
    margin: 40px;
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 5px;
}
h6 {
    margin: 0;
    padding: 8px;
    font-size: 1rem;
    /* background-color: #f5f5f5; */
    border-bottom: 1px solid #ddd;
    
}
.increase-box {
    margin: 20px 20px 40px 100px;
}
.increase-explain {
    display: inline-block;
    text-indent: 2em;
    vertical-align: top;
}
.increase-quill {
    width: 60%;
    display: inline-block;
}
#chooseFile {
    display: none;
}
.option {
    color: #fff;
    background-color: #525288;
}
.preview-img {
    max-width: 500px;
    height: auto;
    display: block;
}
.upload-img {
    padding-bottom: 10px;
    display: inline-block;
    border-bottom: 2px solid #f5f5f5;
}
.upload-img span,
.upload-show > div {
    width: 120px;
    display: inline-block;
    text-align: center;
    font-weight: bold;
}
.upload-show {
    margin-top: 10px;
}
.progress-box {
    vertical-align: middle;
}
.upload-progress {
    width: 60px;
    height: 20px;
    display: inline-block;
    /* background-color: #5cb85c; */
    border-radius: 5px;
}
.progress-show {
    height: 100%;
    display: inline-block;
    background-color: #5cb85c;
    border-radius: 5px;
}
.upload-down {
    border: none;
    border-radius: 3px;
    color: #fff;
    background-color: #5cb85c;
    outline: 0;
}
.upload-remove {
    margin-left: 16px;
    border: none;
    border-radius: 3px;
    color: #fff;
    background-color: #d9534f;
    outline: 0;
}
.option-add {
    margin-top: 40px;
}
.option-add input {
    margin-right: 10px;
    color: #fff;
    background-color: #5cb85c;
}
.add-cancel {
    margin-left: 400px;
}

input:disabled {
    opacity: 0.6;
    cursor: not-allowed;
}
</style>