<template>
  <div>
      <div class="search-article">
                <el-row type="flex" class="row-bg" justify="space-around">
                    <el-col :span="7">
                        <div class="grid-content">
                            <span class="fwb">发布时间</span>
                            <el-date-picker v-model="dateValue1"
                                            type="date"
                                            placeholder="起始日期"
                                            value-format="timestamp"
                                            class="ml16">
                            </el-date-picker>
                        </div>
                    </el-col>
                    <el-col :span="7">
                        <div class="grid-content">
                            <span>——&emsp;</span>
                            <el-date-picker v-model="dateValue2"
                                            type="date"
                                            placeholder="截止日期"
                                            value-format="timestamp"
                                            :picker-options="pickerOptions"
                                            class="ml16">
                            </el-date-picker>
                        </div>
                    </el-col>
                    <el-col :span="7">
                        <div class="grid-content">
                            <span class="fwb">类型&emsp;</span>
                            <select v-model="selectType" class="ml16 select-option">
                                <option 
                                  v-for="(type, index) in types"
                                  v-bind:value="type.value"
                                  v-bind:key="index">{{type.type}}</option>
                            </select>
                        </div>
                    </el-col>
                </el-row>
                <el-row type="flex" class="row-bg el-row-add">
                    <el-col :span="7" :offset="1">
                        <div class="grid-content">
                            <span class="fwb">状态&emsp;</span>
                            <select v-model="selectStatus" class="ml16 select-option">
                                <option 
                                  v-for="(status, index) in statusAll"
                                  v-bind:value="status.value"
                                  v-bind:key="index">{{status.status}}</option>
                            </select>
                        </div>
                    </el-col>
                </el-row>
                <div class="search-button">
                    <input type="button" value="清空" class="button-clear" @click="clearOldData">
                    <input type="button" value="搜索" class="button-checked" @click="searchNewData">
                </div>
            </div>
            <div class="showing-article">
                <el-row>
                    <el-col :span="24">
                        <div class="contents-title">
                            <span class="fwb">Article列表</span>
                            <input type="button" value="+新增" class="article-increase" @click="jumpTo">
                        </div>
                    </el-col>
                </el-row>
                <div class="article-contents">
                    <el-row class="el-row-add">
                        <el-col :span="2"><div class="grid-content contents-align fwb">ID</div></el-col>
                        <el-col :span="6"><div class="grid-content contents-align fwb">名称</div></el-col>
                        <el-col :span="4"><div class="grid-content contents-align fwb">类型</div></el-col>
                        <el-col :span="2"><div class="grid-content contents-align fwb">发布时间</div></el-col>
                        <el-col :span="2"><div class="grid-content contents-align fwb">修改时间</div></el-col>
                        <el-col :span="2"><div class="grid-content contents-align fwb">发布者</div></el-col>
                        <el-col :span="2"><div class="grid-content contents-align fwb">状态</div></el-col>
                        <el-col :span="4"><div class="grid-content contents-align fwb">操作</div></el-col>
                    </el-row>
                    <el-row class="el-row-add contents-list bdt" v-for="(data, index) in returnData" v-bind:key="index">
                        <el-col :span="2"><div class="grid-content contents-align">{{index+1}}</div></el-col>
                        <el-col :span="6"><div class="grid-content contents-align">{{data.title}}</div></el-col>
                        <el-col :span="4">
                            <div class="grid-content contents-align">
                                {{data.type | transformType}}
                            </div>
                        </el-col>
                        <el-col :span="2">
                            <div class="grid-content">
                                <div class="contents-align">{{data.createAt | transformDateFormat_1}}</div>
                                <div class="contents-align">{{data.createAt | transformDateFormat_2}}</div>
                            </div>
                        </el-col>
                        <el-col :span="2">
                            <div class="grid-content bg-purple">
                                <div class="contents-align">{{data.updateAt | transformDateFormat_1}}</div>
                                <div class="contents-align">{{data.updateAt | transformDateFormat_2}}</div>
                            </div>
                        </el-col>
                        <el-col :span="2"><div class="grid-content contents-align">{{data.author}}</div></el-col>
                        <el-col :span="2">
                            <div class="grid-content contents-align">
                                {{data.status == 1? '草稿' : '上线'}}
                            </div>
                        </el-col>
                        <el-col :span="4">
                            <div class="grid-content">
                                <el-col :span="8">
                                    <div class="grid-content contents-align">
                                        <a @click="changeStatus(data.id, data.status)">
                                            {{data.status == 1? '上线' : '下线'}}
                                        </a>
                                    </div></el-col>
                                <el-col :span="8">
                                    <div class="grid-content contents-align">
                                        <a @click="toEditArticle(data.id)">编辑</a>
                                    </div>
                                </el-col>
                                <el-col :span="8">
                                    <div class="grid-content contents-align">
                                        <a @click="deleteArticle(data.id)">删除</a> 
                                    </div>
                                </el-col>
                            </div>
                        </el-col>
                    </el-row>
                </div>
            </div>
            <div class="pagination-block">
                <el-pagination
                    background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 15, 20]"
                    :page-size="10"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="itemTotal">
                </el-pagination>
            </div>
  </div>
</template>

<script>
export default {
    data: function() {
        return {
            returnData: [],
            itemTotal: 0,
            selectType: '',
            selectStatus: '',
            dateValue1: '',
            dateValue2: '',
            currentPage: 1,
            allArguments: {
                startAt: '',
                endAt: '',
                type: '',
                status: '',
                size: 10,
                page: 1
            },
            types: [
                {value: "", type: "全部"},
                {value: 0, type: "首页Banner"},
                {value: 1, type: "找职位Banner"},
                {value: 2, type: "找精英Banner"},
                {value: 3, type: "行业大图"}
            ],
            statusAll: [
                {value: "", status: "全部"},
                {value: 1, status: "草稿"},
                {value: 2, status: "上线"}
            ],
            //日历组件设置选项
            pickerOptions: {
                disabledDate: (time) => {
                    return this.dealDisabledDate(time);
                }
            }
        }
    },
    filters: {
        //转化年月日
        transformDateFormat_1: function(value) {
            let date = new Date(value); //时间戳若为10位需*1000
            let Y = date.getFullYear() + '-';
            let M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
            let D = date.getDate() + ' ';
            return Y+M+D;
        },
        //转化时分秒
        transformDateFormat_2: function(value) {
            let date = new Date(value);
            let h = date.getHours() + ':';
            let m = date.getMinutes() + ':';
            let s = date.getSeconds();
            return h+m+s;
        },
        //转化行业类型
        transformType: function(value) {
            let type = '';
            switch(value){
            case 0:
                type = '首页Banner';
                break;
            case 1:
                type = '找职位Banner';
                break;
            case 2:
                type = '找精英Banner';
                break;
            case 3:
                type = '行业大图';
                break;
            default:
                break; 
            }
            return type;
        }
    },
    created: function() {
        this.$axios.get('api/a/article/search')
        .then(res => {
            // console.log(res);
            if (res.data.code == 0) {
                this.returnData = res.data.data.articleList;
                console.log(this.returnData);
                this.itemTotal = res.data.data.total;
            }else {
                alert("列表项加载失败，错误码："+res.data.code);
            }
        })
    },
    methods: {
        // 单独处理时间的函数
        dealDisabledDate (time) {
        // let compara = new Date(this.dateValue1).getTime();
        return time.getTime() < this.dateValue1;
        },

        //分页器页码变动时触发
        handleSizeChange(val) {
            this.allArguments.size = val;
            this.getDataFormBackend();
        // console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            this.allArguments.page = val;
            this.getDataFormBackend();
            // console.log(`当前页: ${val}`);
        },
        //将起始时间设置为当天零点
        setToZero(val) {
            let translate_1 = new Date(val)
            translate_1.setHours(0)
            translate_1.setMinutes(0)
            translate_1.setSeconds(0)
            return translate_1.getTime()
        },
        //将截止时间设置为当天结束前零点前一秒
        setToEnd(val) {
            let translate_2 = new Date(val)
            translate_2.setHours(23)
            translate_2.setMinutes(59)
            translate_2.setSeconds(59)
            return translate_2.getTime()
        },
        //根据选择条件请求数据
        searchNewData() {
            //当天零点设置
            if (this.dateValue1) {
                this.allArguments.startAt = this.setToZero(this.dateValue1)
            }else {
                this.allArguments.startAt = this.dateValue1;
            }
            //当天末时设置
            if (this.dateValue2) {
                this.allArguments.endAt = this.setToEnd(this.dateValue2)
            }else {
                this.allArguments.endAt = this.dateValue2;
            }
            this.allArguments.type = this.selectType;
            this.allArguments.status =this.selectStatus;
            this.getDataFormBackend();
        },
        //清空搜索选项参数
        clearOldData() {
            for (let key in this.allArguments) {
                this.allArguments[key] = '';
            }
            this.selectType = '',
            this.selectStatus =  '',
            this.dateValue1 =  '',
            this.dateValue2 = '',
            this.getDataFormBackend();
        },
        //获取后台数据
        getDataFormBackend () {
            // console.log(this.allArguments);
            // let request = this.$qs.stringify(this.allArguments);
            this.$axios.get(`api/a/article/search`, {
                params: this.allArguments
            })
            .then(res => {
                if (res.data.code == 0) {
                    this.returnData = res.data.data.articleList;
                    this.itemTotal = res.data.data.total;
                }else {
                    alert("数据加载失败，错误码："+res.data.code)
                }
            })
            .catch(error=>{
                alert(error);
            })
        },

        //路由跳转
        jumpTo() {
            this.$router.push('/article-increase');
        },
        //产品状态改变
        changeStatus(id, status) {
            if (status == 1) {
                alert("你确定将产品上线吗？");
                status = 2;
            }else {
                alert("你确定将产品下线吗？");
                status = 1;
            }
            this.$axios.put(`api/a/u/article/status`, this.$qs.stringify({
                id: id,
                status: status
            })).then(res => {
                if (res.data.code == 0) {
                    alert("操作成功!");
                    this.getDataFormBackend();
                }
            })
        },
        //删除列表项
        deleteArticle(id) {
            alert("确定删除该列表项吗?");
            this.$axios.delete(`api/a/u/article/${id}`)
            .then(res => {
                if (res.data.code == 0) {
                    alert("删除成功");
                    this.getDataFormBackend();
                }
            })
        },
        //跳转编辑列表项
        toEditArticle(id) {
            this.$router.push({
                path: '/article-increase',
                query: {
                   id: id
                } 
            })
        },
    }
}
</script>

<style scoped>
.ht100 {
    height: 100%;
}
.db {
    display: inline-block;
}
.ml16 {
    margin-left: 16px;
}
.fwb {
    font-weight: bold;
}
.bdt {
    border-top: 1px solid #ddd;
}

.select-option {
    width: 220px;
    height: 40px;
    padding-left: 10px;
    padding-right: 10px;
    border: 0;
    border-radius: 4px;
    box-shadow:  0 0 2px #ddd;
}
.search-button {
    margin-bottom: 10px;
    text-align: right;
}
.button-clear {
    margin-right: 16px;
    padding: 5px 8px;
    color: #fff;
    background-color: #d9534f;
    border-radius: 4px;
    border: none;
    outline: 0;
    
}
.button-checked {
    margin-right: 30px;
    padding: 5px 8px;
    color: #fff;
    background-color: #42A948;
    border-radius: 4px;
    border: none;
    outline: 0;
}

.showing-article {
    margin-top: 30px;
    border-radius: 5px;
    border: 1px solid #ddd;
}
.contents-title {
    padding: 10px 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #ddd;
}
.article-increase {
    color: #fff;
    background-color: #42A948;
    border: none;
    border-radius: 4px;
    outline: 0;
}
.search-article {
    background-color: #fcfcfc; 
    border-radius: 6px;
    border: 1px solid #ddd;
  }
  .el-row {
    margin-bottom: 20px;
    /* &:last-child {
      margin-bottom: 0;
    } */
  }
  .el-row-add {
      margin-bottom: 0;
  }
  .el-col {
    border-radius: 4px;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    /* background-color: #f9fafc; */
  }
.article-contents {
    margin: 16px;
    background-color: #fcfcfc;
    border: 1px solid #ddd;
    border-radius: 4px;
}
.contents-list  {
    padding-top: 16px;
    padding-bottom: 16px;
}
.contents-align {
/* text-align: center; */
display: flex;
justify-content: center;
align-items: center;
word-break: break-all;
}
.pagination-block {
    margin-top: 16px;
}

a {
    color: #337ab7;
    cursor: pointer;
}
a:hover {
    color: #525288;
}

</style>