<template>
    <div class="login-backend">
        <h5>后台管理系统</h5>
        <form>
            <div class="mt20">
                <label for="">账号</label>
                <input type="text" class="login-infor" placeholder="请输入账号名" v-model="account" maxlength="15">
            </div>
            <div class="mt20">
                <label for="">密码</label>
                <input type="password" class="login-infor" placeholder="请输入密码" v-model="pwd" maxlength="15">
            </div>
            <div class="mt20 backend-message">{{message}}</div>
            <input class="login" @click="loginBackend()" value="登 录" type="button"> 
            
        </form>
    </div>
</template>

<script>

export default {
    name: 'login',
    data: function() {
        return {
            message: '',
            account: '',
            pwd: ''
        }
        
    },
    methods: {
        loginBackend: function() {
            if (this.account == '' || this.pwd == '') {
                this.message = '输入不能为空！';
            }else {
                this.$axios.post('api/a/login', this.$qs.stringify({
                name: this.account,
                pwd: this.pwd}))
                .then(response => {
                console.log(response);
                if (response.data.code == 0) {
                    sessionStorage.setItem('isLogin', 'true');
                    this.$router.push('/articleHome');
                }else {
                    this.message = response.data.message;
                }
                })
            }
        
    }
    }
    
    
}
</script>

<style scoped>
.login-backend {
    margin-top: 10%;
    margin-left: 50%;
    padding: 16px;
    transform: translateX(-50%);
    width: 580px;
    height: 360px;
    font-size: 2rem;
    text-align: center;
    background-color: rgba(255, 255, 255, .4);
    border-radius: 30px;
}
h5 {
    margin: 0;
    font-size: 2.5rem;
}
.mt20 {
    margin-top: 20px;
}
.login-infor {
    margin-left: 30px;
    padding-left: 8px;
    height: 3rem;
    vertical-align: middle;
    font-size: 1.5rem;
    border: none;
    border-radius: 30px;
    border-left: 30px solid #346c9c;
    outline: 0;
    box-shadow: 0px 0px 2px #444;
}
.backend-message {
     color: #a94442;
}
button,
.login {
    margin: 2rem;
    padding: 10px 36px;
    font-size: 1.5rem;
    background-color: #346c9c;
    color: #fff;
    border-radius: 36px;
    border: none;
    outline: 0;
}
</style>